import React, { useEffect } from "react";
import "./styles.css";

export const handler = ({ inputs, mechanic }) => {
  const {
    width,
    height,
    columns,
    columnWidth,
    columnGap,
    outsideGap,
    url,
    columnColor,
  } = inputs;

  let colPaths = [];
  for (let i=0; i < columns; i++) {
    let x = outsideGap + (columnWidth * i) + (columnGap * i)
    colPaths.push(<rect key={i} fill={columnColor} stroke="none" x={x} y="0" width={columnWidth} height={height} />)
  }
  console.log(colPaths);


  useEffect(() => {
    mechanic.done();
  }, []);

  return (
    <>
    <svg class="grid" width={width} height={height}>
      <rect fill="none" stroke="black" width={width} height={height} />
      {colPaths}
    </svg>
    <iframe class="grid" width={width} height={height} src={url} />
    </>
  );
};

export const inputs = {
  width: {
    type: "number",
    default: 1220,
  },
  height: {
    type: "number",
    default: 1000,
  },
  columns: {
    type: "number",
    default: 12,
  },
  columnWidth: {
    type: "number",
    default: 100,
  },
  columnGap: {
    type: "number",
    default: 10,
  },
  columnColor: {
    type: "color",
    default: "rgba(50,100,150,0.5)",
  },
  outsideGap: {
    type: "number",
    default: 10,
  },
  url: {
    type: "text",
    default: "https://www.washingtonpost.com",
  },
}

export const settings = {
  engine: require("@mechanic-design/engine-react"),
  usesRandom: false,
};
